import React from 'react';
import "./skillsstyles.css"
import Circle from 'react-circle';

export default class Skills extends React.Component {

    render() {

        const skills = [
                {
                    Name: "React.js",
                    BarColor: "#00d8ff",
                    percentage: 90
                },
                {
                    Name: "Java",
                    BarColor: "#F8981D",
                    percentage: 90
                },

                {
                    Name: "Android",
                    BarColor: "#A4C639",
                    percentage: 90
                },
                {
                    Name: "Python",
                    BarColor: "#366E9D",
                    percentage: 90
                },
                {
                    Name: "C/C++",
                    BarColor: "#68227B",
                    percentage: 65
                },
                {
                    Name: "Linux",
                    BarColor: "#FCB713",
                    percentage: 70
                },
                {
                    Name: "HTML/CSS",
                    BarColor: "#A01D21",
                    percentage: 80
                },
                {
                    Name: "SQL",
                    BarColor: "#333333",
                    percentage: 65
                },

            ]
        ;

        const backgroundColor = "#DCDCDC";

        return (
            <div className="Skills">
                {skills.map((skill, i) => <div className="Skill" key={i}>
                    <Circle
                        progress={skill.percentage}
                        bgColor={backgroundColor}
                        progressColor={skill.BarColor}
                        textColor={"black"}
                        lineWidth={35}
                    />
                    <h3>{skill.Name}</h3>
                </div>)}

            </div>
        )
    }
}


